@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  :root {
    /* Purple Premium Theme - Dark Mode Only */
    --background: 260 40% 5%;
    --background-secondary: 260 35% 8%;
    --foreground: 260 10% 95%;
    
    --card: 260 35% 10%;
    --card-foreground: 260 10% 95%;
    --card-hover: 260 35% 14%;
    
    --popover: 260 35% 10%;
    --popover-foreground: 260 10% 95%;
    
    /* Primary - Vibrant Purple */
    --primary: 270 70% 60%;
    --primary-foreground: 260 10% 98%;
    --primary-glow: 270 80% 65%;
    --primary-muted: 270 50% 45%;
    
    /* Secondary - Deep Purple */
    --secondary: 260 40% 18%;
    --secondary-foreground: 260 20% 90%;
    
    /* Accent - Cyan/Teal for contrast */
    --accent: 185 70% 55%;
    --accent-foreground: 260 10% 98%;
    --accent-glow: 185 80% 60%;
    
    /* Muted Colors */
    --muted: 260 30% 15%;
    --muted-foreground: 260 15% 60%;
    
    /* State Colors */
    --destructive: 0 70% 55%;
    --destructive-foreground: 0 0% 100%;
    --success: 145 65% 45%;
    --success-foreground: 0 0% 100%;
    --warning: 38 92% 50%;
    --warning-foreground: 0 0% 10%;
    
    /* Borders & Inputs */
    --border: 260 30% 20%;
    --border-glow: 270 60% 50%;
    --input: 260 30% 15%;
    --ring: 270 70% 60%;
    
    /* Gradients */
    --gradient-primary: linear-gradient(135deg, hsl(270 70% 60%), hsl(285 65% 55%));
    --gradient-accent: linear-gradient(135deg, hsl(185 70% 55%), hsl(200 70% 50%));
    --gradient-purple-cyan: linear-gradient(135deg, hsl(270 70% 50%), hsl(185 70% 45%));
    --gradient-background: linear-gradient(180deg, hsl(260 40% 5%), hsl(260 35% 8%), hsl(260 40% 5%));
    --gradient-card: linear-gradient(145deg, hsl(260 35% 12%), hsl(260 35% 8%));
    --gradient-glow: radial-gradient(circle at 50% 50%, hsl(270 70% 60% / 0.15), transparent 70%);
    
    /* Shadows */
    --shadow-sm: 0 2px 8px hsl(270 70% 20% / 0.3);
    --shadow-md: 0 4px 20px hsl(270 70% 20% / 0.4);
    --shadow-lg: 0 8px 40px hsl(270 70% 20% / 0.5);
    --shadow-glow: 0 0 30px hsl(270 70% 60% / 0.3);
    --shadow-glow-accent: 0 0 30px hsl(185 70% 55% / 0.3);
    --shadow-card: 0 4px 24px hsl(260 50% 5% / 0.5), 0 0 0 1px hsl(260 30% 20% / 0.5);
    
    /* Transitions */
    --transition-fast: 0.15s cubic-bezier(0.4, 0, 0.2, 1);
    --transition-base: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    --transition-slow: 0.5s cubic-bezier(0.4, 0, 0.2, 1);
    --transition-bounce: 0.5s cubic-bezier(0.34, 1.56, 0.64, 1);
    
    /* Glassmorphism */
    --glass-bg: hsl(260 35% 15% / 0.6);
    --glass-border: hsl(260 30% 30% / 0.3);
    --glass-blur: blur(16px);
    
    /* Spacing */
    --radius: 0.75rem;
    --radius-lg: 1rem;
    --radius-xl: 1.5rem;
  }
}

@layer base {
  * {
    @apply border-border;
  }
  
  html {
    scroll-behavior: smooth;
  }
  
  body {
    @apply bg-background text-foreground antialiased;
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
    background: var(--gradient-background);
    min-height: 100vh;
  }
  
  /* Custom Scrollbar */
  ::-webkit-scrollbar {
    width: 8px;
    height: 8px;
  }
  
  ::-webkit-scrollbar-track {
    background: hsl(var(--background-secondary));
  }
  
  ::-webkit-scrollbar-thumb {
    background: hsl(var(--primary) / 0.5);
    border-radius: 4px;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background: hsl(var(--primary) / 0.7);
  }
  
  /* Selection */
  ::selection {
    background: hsl(var(--primary) / 0.3);
    color: hsl(var(--foreground));
  }
}

@layer components {
  /* Glassmorphism Card */
  .glass-card {
    background: var(--glass-bg);
    backdrop-filter: var(--glass-blur);
    -webkit-backdrop-filter: var(--glass-blur);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-lg);
  }
  
  /* Glow Effects */
  .glow-primary {
    box-shadow: var(--shadow-glow);
  }
  
  .glow-accent {
    box-shadow: var(--shadow-glow-accent);
  }
  
  .glow-text {
    text-shadow: 0 0 20px hsl(var(--primary) / 0.5);
  }
  
  /* Gradient Text */
  .gradient-text {
    background: var(--gradient-primary);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  
  .gradient-text-accent {
    background: var(--gradient-accent);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  
  /* Animated Background Orbs */
  .bg-orb {
    position: absolute;
    border-radius: 50%;
    filter: blur(80px);
    opacity: 0.4;
    pointer-events: none;
  }
  
  .bg-orb-primary {
    background: hsl(var(--primary));
  }
  
  .bg-orb-accent {
    background: hsl(var(--accent));
  }
  
  /* Animated Border */
  .animated-border {
    position: relative;
    background: hsl(var(--card));
    border-radius: var(--radius-lg);
  }
  
  .animated-border::before {
    content: '';
    position: absolute;
    inset: -1px;
    border-radius: var(--radius-lg);
    padding: 1px;
    background: linear-gradient(
      var(--border-angle, 0deg),
      hsl(var(--primary)),
      transparent 40%,
      transparent 60%,
      hsl(var(--accent))
    );
    -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
    mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
    -webkit-mask-composite: xor;
    mask-composite: exclude;
    animation: border-rotate 4s linear infinite;
  }
  
  @keyframes border-rotate {
    to {
      --border-angle: 360deg;
    }
  }
  
  @property --border-angle {
    syntax: "<angle>";
    initial-value: 0deg;
    inherits: false;
  }
  
  /* Shine Effect */
  .shine-effect {
    position: relative;
    overflow: hidden;
  }
  
  .shine-effect::after {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: linear-gradient(
      to right,
      transparent 0%,
      hsl(var(--primary) / 0.1) 50%,
      transparent 100%
    );
    transform: rotate(45deg) translateX(-100%);
    transition: transform 0.6s ease;
  }
  
  .shine-effect:hover::after {
    transform: rotate(45deg) translateX(100%);
  }
  
  /* Floating Animation */
  .float {
    animation: float 6s ease-in-out infinite;
  }
  
  @keyframes float {
    0%, 100% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-10px);
    }
  }
  
  /* Pulse Glow */
  .pulse-glow {
    animation: pulse-glow 2s ease-in-out infinite;
  }
  
  @keyframes pulse-glow {
    0%, 100% {
      box-shadow: 0 0 20px hsl(var(--primary) / 0.3);
    }
    50% {
      box-shadow: 0 0 40px hsl(var(--primary) / 0.5);
    }
  }
  
  /* Underline Animation */
  .underline-animation {
    position: relative;
  }
  
  .underline-animation::after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: -4px;
    width: 0;
    height: 2px;
    background: hsl(var(--foreground));
    transition: all var(--transition-base);
    transform: translateX(-50%);
    border-radius: 2px;
  }
  
  .underline-animation:hover::after {
    width: 100%;
  }
  
  /* Icon Container */
  .icon-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 48px;
    height: 48px;
    border-radius: 12px;
    background: hsl(var(--primary) / 0.15);
    transition: all var(--transition-base);
  }
  
  .icon-container:hover {
    background: hsl(var(--primary) / 0.25);
    transform: scale(1.05);
  }
  
  /* Noise Texture Overlay */
  .noise-overlay::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.03;
    pointer-events: none;
    background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 256 256' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.85' numOctaves='4' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)'/%3E%3C/svg%3E");
  }
}

@layer utilities {
  .text-balance {
    text-wrap: balance;
  }
  
  .transition-smooth {
    transition: all var(--transition-base);
  }
  
  .transition-bounce {
    transition: all var(--transition-bounce);
  }
}

/* Debug wrapper support */
@layer base {
  [data-debug-wrapper="true"] {
    display: contents !important;
  }
}
